import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController } from 'ionic-angular';
import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_type, sc_code } from '../../config';

/**
 * Generated class for the PayslipDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-payslip-detail',
  providers: [SoapService], 
  templateUrl: 'payslip-detail.html',
})
export class PayslipDetailPage {
  payslipDetail: any;
  isLoading: Boolean = true;
  periodGroup: any;
  nipp: any;
  userdataTPK: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,    
  ) {
    this.periodGroup = navParams.get('periodGroup');
    this.nipp = navParams.get('nipp');
    this.storage.get('userdataTPK').then(val => {  
      this.userdataTPK = val;
      console.log(this.userdataTPK);
      this.getDetail();
    });
    

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PayslipDetailPage');
  }

  getDetail() {
    this.soapService
      .post("http://103.19.80.188/api/index.php?wsdl", 'eoffice_payslip_detail', {
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            nipp: this.nipp , 
            period_group: this.periodGroup
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));        
        console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.payslipDetail = responData;
        } else {          
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan detail payslip, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();  
        }
        this.isLoading = false;
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan detail payslip, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();         
        this.isLoading = false;
      });
  }

}



